import axios from "axios"
import { useEffect, useState } from "react"
import { useParams, useNavigate } from "react-router"
import styles from './fullPizza.module.scss'
import NotFound from "./NotFound"

export const FullPizza = () => {
    const { id } = useParams() //  id   .   id    <Link to={`/fullPizza/${id}`}></Link>   "PizzaBlock"
                               //    ,    useParams          console.log().  :
                               // const name = useParams()   -----  
                               // console.log(name)          ----- ,    

    const navigate = useNavigate()


    const [ pizza, setPizza ] = useState()
    const [ textLorem, setTextLorem ] = useState('')

    useEffect(() => {
        async function fetchPizza() {
            try {
               const { data } = await axios.get('https://668ff050c0a7969efd9a3ea7.mockapi.io/items/' + id)
               setPizza(data)
            } catch (error) {
                console.log("   : " + error)
                navigate('*')
            }
        }
        fetchPizza()

        async function fetchTextLorem() {
            try {
               const { data } = await axios.get('https://fish-text.ru/get')
               setTextLorem(data)
            } catch (error) {
                console.log("  : " + error)
            }
        }
        fetchTextLorem()
    }, [])

    if(!pizza) {
        return <div>...</div>
    }

  return (
    <div className="container">
        <div className={styles.fullPizzaContainer}>
            <img src={pizza.imageUrl} alt={pizza.title} />
            <div className={styles.right}>
                <div className={styles.description}>
                <h2>{pizza.title}</h2>
                <p>{textLorem.text}</p>
                </div>
                <h3>{pizza.price} </h3>
            </div>
        </div>
    </div>
  )
}

export default FullPizza
