import React from 'react';
import ReactDOM from 'react-dom/client';
import { BrowserRouter } from 'react-router-dom' //  ,     react-router  react-router-dom
import './index.css';
import App from './App.jsx';

import { store } from './redux/store';    // store    @reduxjs/toolkit 

import { Provider } from 'react-redux';   // Provider    react-redux ,    ,   redux  react ,
                                          //   @reduxjs/toolkit   ,     react


const root = ReactDOM.createRoot(document.getElementById('root'))

root.render(
  <Provider store={store}>
      <BrowserRouter>
          <App />
      </BrowserRouter>
  </Provider>
);


