import React from 'react';
import {Routes, Route} from 'react-router-dom'


import Home from './pages/Home';
import NotFound from './pages/NotFound';
import Cart from './pages/Cart';
import FullPizza from './pages/FullPizza.jsx';
import './scss/app.scss';
import MainLayout from './layouts/MainLayout';

// -------------------------------------------------------------      React.createContext('') ---------------------------------------------------

// 1.    App        : export const SearchContext = React.createContext('')
// 2.    App   : const [searchValue, setSearchValue] = React.useState('')
// 3.       <SearchContext.Provider></SearchContext.Provider>
// 4.   SearchContext.Provider   value,         
//  :  <SearchContext.Provider value={{searchValue, setSearchValue}}>
// 5.      : import { SearchContext } from '../../App'
// 6.   useContext(SearchContext),      'SearchContext'   ,
//     value: const {searchValue, setSearchValue} = React.useContext(SearchContext)
// 7.   :)

// ---------------------------------------------------------------------------------------------------------------------------------------------------------------------------------


export const SearchContext = React.createContext('')  //     ,    . 
                                                      //        ,   ,  
                                                      //    ,     Provider. Provider   . 
                                                      //            
                                                      //       Provider. ..   : SearchContext.Provider
                                                      //   export  ,  ,        


function App() {
  const [searchValue, setSearchValue] = React.useState('') //   useState   ,    React.createContext('')   
                                                           //  SearchContext,           SearchContext.Provider
                                                           //    value     useState.        
                                                           //   React.useContext()     'SearchContext'   
                                                           // : React.useContext(SearchContext).       ,
                                                           //   React.useState    value   . 
                                                           // : const {searchValue, setSearchValue} = React.useContext(SearchContext)
                                                           //    export   

  return (
    <SearchContext.Provider  value={{searchValue, setSearchValue}}>
      <Routes>
          <Route path='/' element={<MainLayout />}>
              <Route path="" element={<Home />}/>
              <Route path="cart" element={<Cart />}/>
              <Route path="fullPizza/:id" element={<FullPizza />}/>   {/*        ,      */}
              <Route path="*" element={<NotFound />}/>
          </Route>
      </Routes>
    </SearchContext.Provider>
  )
}

export default App;

