import React from "react";
import axios from "axios";
import qs from "qs"; //  qs   QueryString

import Categories from "../components/Categories"
import Sort from "../components/Sort"
import PizzaBlock from '../components/PizzaBlock/PizzaBlock'
import Skeleton from '../components/PizzaBlock/Skeleton';
import Pagination from "../components/Pagination";

import { useNavigate } from "react-router";
import { SearchContext } from "../App.jsx"; //   
import { useDispatch, useSelector } from "react-redux";
import filterSlice, { setPage, setFilters, filterSelector } from "../redux/slices/filterSlice";
import { fetchPizzas, fetchPizzasDefault, pizzaSelector } from "../redux/slices/pizzaSlice";
import { Link } from "react-router-dom";


//     pizzas.json   ,    mockapi.io :  https://668ff050c0a7969efd9a3ea7.mockapi.io/items

// 
// 




const Home = () => {
  const navigate = useNavigate()
  const isSearchStrokeRef = React.useRef(false)
  const isMounted = React.useRef(false) // Mounted   , ..  -  ()  
  const { searchValue } = React.useContext(SearchContext) //  (useContext)
  
  const { categoryID, popupSort, pageInitObj } = useSelector(filterSelector)
  const { pizzasOnBackEndDefault, pizzasOnBackEnd, status } = useSelector(pizzaSelector)

  const dispatch = useDispatch()

  let noGrid = status === 'error' ? 'no-grid' : ''
  let orderFlag = popupSort.rotateSortSVG === 'rotate_180' ? 'desc' : 'asc'  // asc  desc         .      mockapi.io
                                                                             // ..    fetch    order=desc( )  order=asc( )
                                                                                  
  const search = searchValue ? `&title=${searchValue}` : ''
 
  function fetchData() {
    dispatch(fetchPizzasDefault({
      categoryID,
      sort: popupSort.sort,
      search,
      orderFlag
    }))

    if (!Object.keys(qs.parse(window.location.search.substring(1))).length === 0) { //      
      dispatch(setFilters({...qs.parse(window.location.search.substring(1))}))
    }
    dispatch(setPage({...pageInitObj, allItemsFromServer: pizzasOnBackEndDefault.length}))

    dispatch(fetchPizzas({
          categoryID,
          pageCount: pageInitObj.pageCount,
          limitItemsOnPage: pageInitObj.limitItemsOnPage,
          sort: popupSort.sort,
          search,
          orderFlag
        }))
  }

//-----------------------------------    START -----------------------------------------
  React.useEffect(() => {
    if(isMounted.current) {
      const queryString = qs.stringify({
        category: categoryID,
        page: pageInitObj.pageCount,
        sortBy: popupSort.sort,
      })
    
      navigate(`?${queryString}`)
    }
    isMounted.current = true
    },[categoryID, pageInitObj.pageCount, popupSort.sort]
  )
//-----------------------------------    END -------------------------------------------


//-----------------------------------        START --------------------------------------------
  React.useEffect(() => {
    if(window.location.search) {
      const params = qs.parse(window.location.search.substring(1))

      dispatch(setFilters({...params}))
      isSearchStrokeRef.current = true
    }
  },[categoryID, pageInitObj.pageCount, popupSort.sort, orderFlag])
//----------------------------------        END ------------------------------------------------


  //-----------------------------------------------------    START --------------------------------------------------
  React.useEffect(() => {
    fetchData()
    if(isSearchStrokeRef.current) {
      fetchData()
    }
    window.scrollTo(0, 0) //        fetch      ,
                            //        x()  y()  (0, 0)
    },[categoryID, popupSort, searchValue, search, pageInitObj.pageCount, pizzasOnBackEndDefault.length])
  //----------------------------------------------------    END -------------------------------------------------------

  const skeletons = [...new Array(13)].map((_, i) => <Skeleton key={i} />)
  return (
    <>
      <div className="content__top">
          <Categories />

          <Sort />
      </div>

      <h2 className="content__title"> </h2>

      <div className={`content__items ${noGrid}`}>
        {status === 'loading'
          ? skeletons 
          : (status === 'error'
                        ? <p className="not-found">     ,           </p> 
                        : pizzasOnBackEnd.map((pizza, i) => <PizzaBlock {...pizza} key={pizza.id} />)
            )
        }
      </div>

      {status === 'error' ? '' : <Pagination />}
    </>
  )
}

export default Home